import React from 'react';
import { connect } from 'react-redux';
import { onOpenModelSelect, onSaveSetting, nameChange } from '../../redux/reducers/mainReducer';
import SettingsPage from './SettingsPage';


let mapStateToProps = (state) => {
    
    return {
        newName: state.main.newName,
        namePattern: state.main.selectPattern.namePattern,
        patterns: state.main.patterns,
        selectPattern: state.main.model,
        openModel: state.main.openModel,
        alertMessage: state.main.alertMessage,
        windowEditPattern: state.main.windowEditPattern
    }
}


const SettingsPageContainer = connect(mapStateToProps, {
//лайфхак с mapDispatchToProps передаем не функию а объект, где колбек функии образовываются сами
        onSaveSetting,
        nameChange,
        onOpenModelSelect

})(SettingsPage)


export default SettingsPageContainer;