import React from 'react';
import './page.scss';
import Header from '../Header/Header';
import MainContainer from '../Main/MainContainer';
import SidebarContainer from '../Sidebar/SidebarContainer';

const Page = (props) => {

    return (
        <div className='page'>
            <Header />

            <div className='container'>
                <SidebarContainer />
                <MainContainer />
            </div>
        </div>
    );
}

export default Page;