import React from 'react';
import './PatternStore.scss';

const PatternStore = (props) => {
    return (
        <div className='pattern-store'>

            <div className='pattern-store-header'>
                <div className="store-header-footer">
                    <p>адрес</p>
                    <p>контактные данные</p>
                    <p>соц. сети</p>
                </div>

                <div className="store-header-navbar">
                   <p>лого</p>
                   <p>навигация</p>
                   <p>корзина товаров</p>
                   <p>вход в кабинет</p>
                </div>
            </div>

            <div className='pattern-store-main'>

                <div className="pattern-store-sidebar">
                    <h1 className='store-sidebar-header'>каталог товаров</h1>

                    <ul className='store-sidebar-catalog'>
                        <li><a>товары 1</a></li>
                        <li><a>товары 2</a></li>
                        <li><a>товары 3</a></li>
                        <li><a>товары 4</a></li>
                        <li><a>товары 5</a></li>
                    </ul>
                </div>

                <div className='pattern-store-content'>
                    <p>контент</p>
                </div>

            </div>
        </div> 
    );
}

export default PatternStore;