import React from 'react';
import { connect } from 'react-redux';
import { onOpenCreaturePage } from '../../redux/reducers/mainReducer';
import Main from './Main';

let mapStateToProps = (state) => {
    
    return {
        openCreaturePage: state.main.openCreaturePage,
        openModel: state.main.openModel,
        windowEditPattern: state.main.windowEditPattern
    }
}


const MainContainer = connect(mapStateToProps, {
    onOpenCreaturePage
})(Main)

export default MainContainer;