import React from 'react';
import PatternContainer from './Pattern/PatternContainer';

const ModelSelect = (props) => {

    let pattern = props.patterns.map(el => {
        return (
            <PatternContainer 
                name={el.name} 
                pattern={el.pattern} 
                selectPattern={props.selectPattern.item} 
                patterns={props.patterns}
                dispatch={props.dispatch}/>
        )
    })

    let select = props.selectPattern.item;
    
    return (
        <div className='model-window'>
            <div className='model'>
                
                { pattern[select] }

            </div>
        </div>
    );
}

export default ModelSelect;