import React from 'react';
import './PatternVisit.scss';

const PatternVisit = (props) => {
    return (
        <div className='pattern-visit'>

            <div className='pattern-visit-header'>
                <div className='visit-header-info'>
                    <p>адрес</p><p>контактные данные</p><p>соц. сети</p>
                </div>
                <div className='visit-name'>
                    <p>логотип компании</p><p>бургер навигация</p>
                </div>
            </div>

            <div className='pattern-visit-content'>
                <p>контент</p>
            </div>
        </div> 
    );
}

export default PatternVisit;