import React from 'react';
import WebServices from '../Patterns/WebServices/WebServices';
import WebStore from '../Patterns/WebStore/WebStore';
import WebVisit from '../Patterns/WebVisit/WebVisit';
import SettingsPageContainer from '../SettingsPage/SettingsPageContainer';
import Creator from './Creator/Creator';
import './main.scss';

const Main = (props) => {
    
    return (
        <div className={props.openModel ? 'main' + ' hidden' : 'main'}>

            {props.openCreaturePage ?
            <>
                <SettingsPageContainer />
                {+props.windowEditPattern === 1? 
                <WebVisit />:
                +props.windowEditPattern === 2? 
                <WebServices />:
                +props.windowEditPattern === 3? 
                <WebStore />:
                <div className='no-select-pattern'>Шаблон не выбран</div>}
                
            </> : 
            <Creator onOpenCreaturePage={props.onOpenCreaturePage}/>
            }  
        </div>
    );
}

export default Main;