const SHOW_SIDEBAR = 'SHOW_SIDEBAR';

let initialState = {
    showSidebar: true
};

const sidebarReducer = (state = initialState, action) => {
    
    switch(action.type) {
        case SHOW_SIDEBAR: {
            let copyState = {...state}
            copyState.showSidebar = !copyState.showSidebar;
            return copyState;
        }    
        default:
            return state
    }
}


export let onShowSidebar = () => {
    return {
        type: SHOW_SIDEBAR
    }
}


export default sidebarReducer;