const OPEN_SETTING_PAGE = 'OPEN_SETTING_PAGE';
const OPEN_SELECT_PATTERN = 'OPEN_SELECT_PATTERN';
const CLOSE_SELECT_PATTERN = 'CLOSE_SELECT_PATTERN';
const NEXT_AND_PREV_PATTERN = 'NEXT_AND_PREV_PATTERN';
const VALUE_RADIO_BTN = 'VALUE_RADIO_BTN';
const ADD_SAVE_PAGE = 'ADD_SAVE_PAGE';
const UPDATE_NEW_NAME_PAGE = 'UPDATE_NEW_NAME_PAGE';
const DELETE_SAVE_SIDEBAR = 'DELETE_SAVE_SIDEBAR';


let initialState = {

    openCreaturePage: false,
    openModel: false,
    windowEditPattern: 0,

    patterns: [
        {name: 'Сайт визитка', pattern: 1},
        {name: 'Сайт услуг', pattern: 2},
        {name: 'Интернет магазин', pattern: 3}
    ],

    selectPattern: {
        namePattern: 'шаблон не выбран'
    },

    model: {
        item: 0
    },

    pages: JSON.parse(localStorage.getItem('savePages')),
    newName: '',

    alertMessage: 0
};

if(localStorage.getItem('savePages') !== null) {
    
}



const mainReducer = (state = initialState, action) => {
    
    switch(action.type) {
        case OPEN_SETTING_PAGE: {
            
            let copyState = {...state}
            copyState.openCreaturePage = true;
            copyState.windowEditPattern = action.numberPattern || 0
            copyState.selectPattern.namePattern = action.oldNamePattern || copyState.selectPattern.namePattern
            copyState.newName = action.oldValue || ''
            return copyState;
        }
    
        case OPEN_SELECT_PATTERN: {
            let copyState = {...state}
            copyState.openModel = true;

            return copyState;
        }
    
        case CLOSE_SELECT_PATTERN: {
            let copyState = {...state}
            copyState.openModel = false;
            copyState.model.item = 0;
            return copyState;
        }   

        case VALUE_RADIO_BTN: {
            let copyState = {...state}
            copyState.windowEditPattern = action.valueBtn;
            copyState.selectPattern.namePattern = action.namePattern
            
            return copyState;
        }
            
        case ADD_SAVE_PAGE: {
            let copyState = {...state}
            
            if(copyState.pages === null) {
                copyState.pages = [];
            } else {
                copyState.pages = [...state.pages];
            }
            
            if(copyState.newName !== '' && copyState.windowEditPattern !== 0) {
                copyState.pages.push({name: copyState.newName, pattern: copyState.selectPattern.namePattern, id: copyState.pages.length, numberPattern: copyState.windowEditPattern})
                localStorage.setItem('savePages', JSON.stringify(copyState.pages))
                copyState.pages = JSON.parse(localStorage.getItem('savePages'))
                copyState.newName = '';
                copyState.alertMessage = 0;
            } else if (copyState.newName === '') {
                copyState.alertMessage = 1;
                
            } else if (copyState.windowEditPattern === 0) {
                copyState.alertMessage = 2;
                
            }
            
            
            return copyState;
        }

        case UPDATE_NEW_NAME_PAGE: {
            let copyState = {...state}
            copyState.newName = action.newName
            
            return copyState;
        }
            
        case NEXT_AND_PREV_PATTERN: {
            let copyState = {...state}
            switch(action.valueArrowBtn) {
                case 'next':
                    if(copyState.model.item < 2) {
                        copyState.model.item += 1
                     } else {
                        copyState.model.item = 0 // если число в item будет больше длинны массива то слайдер начнет сначала
                     } 
                break;
        
                case 'prev':
                    if(copyState.model.item > 0) {
                        copyState.model.item -= 1
                    } else {
                        copyState.model.item = 2 // 2 это последний шаблон в массиве
                    } 
                break;
        
                default:
                    return copyState.model.item = 0
            }
            return copyState;
        }

        case DELETE_SAVE_SIDEBAR: {
            let copyState = {...state}
            copyState.pages.splice(action.idItem, 1);
            localStorage.setItem('savePages', JSON.stringify(copyState.pages))
            
            return copyState;
        }
           
        default:
            return state
    }
}


export let onOpenCreaturePage = (number, name, value) => {
    return {
        type: OPEN_SETTING_PAGE,
        numberPattern: number,
        oldNamePattern: name,
        oldValue: value
    }
}

export let onOpenModelSelect = () => {
    return {
        type: OPEN_SELECT_PATTERN
    }
}


export let onCloseModelSelect = () => {
    return {
        type: CLOSE_SELECT_PATTERN
    }
}

export let checkedBtn = (value, name) => {
    return {
        type: VALUE_RADIO_BTN, 
        valueBtn: value, 
        namePattern: name
    }
}

export let onAddValueNextAndPrev = (value) => {
    return {
        type: NEXT_AND_PREV_PATTERN,
        valueArrowBtn: value
    }
}

export let onSaveSetting = () => {
    return {
        type: ADD_SAVE_PAGE
    }
}

export let nameChange = (name) => {
    return {
        type: UPDATE_NEW_NAME_PAGE,
        newName: name
    }
}

export let onDeleteItem = (id) => {
    
    return {
        type: DELETE_SAVE_SIDEBAR,
        idItem: id
    }
}


export default mainReducer;