import React from 'react';
import './pattern.scss';
import PatternServices from './Patterns/PatternServices';
import PatternStore from './Patterns/PatternStore';
import PatternVisit from './Patterns/PatternVisit';


const Pattern = (props) => {

    let valuePrev = React.createRef();
    let valueNext = React.createRef();
    let redioBtn = React.createRef();

    let closeModelSelect = () => {
        props.onCloseModelSelect()
    }

    let addValuePrev = () => {
        let value = valuePrev.current.value;
        props.onAddValueNextAndPrev(value)
    }

    let addValueNext = () => {
        let value = valueNext.current.value;
        props.onAddValueNextAndPrev(value)
    }

    let checkedRadioBtn = () => {
        let name = props.name;
        let value = redioBtn.current.value;
        props.checkedBtn(value, name)
    }
    
    return (
        <li>

            <div className='model-header'>
                <div>
                    <input id={`pattern-${props.pattern}`} type='radio' name ="radio" value={props.pattern} 
                        onClick={checkedRadioBtn}
                        ref={redioBtn}/>
                    <label htmlFor={`pattern-${props.pattern}`}>{props.name}</label>
                </div>

                <div>
                    <button className='btn-select' value='prev' ref={valuePrev} onClick={addValuePrev}>&lt;</button>
                    <span>{props.patterns[props.selectPattern].pattern}</span>
                    <button className='btn-select' value='next' ref={valueNext} onClick={addValueNext}>&gt;</button>
                    <button className='close-model' onClick={closeModelSelect}>&#10006;</button>
                </div>
            </div>

            { props.pattern === 1 ? <PatternVisit /> : props.pattern === 2 ? <PatternServices /> : <PatternStore />}
       
        </li>  
    );
}

export default Pattern;