import React from 'react';
import './nav.scss';
import Edit from './Items/Edit/Edit';
import Projects from './Items/Projects/Projects';
import Settings from './Items/Settings/Settings';
import Help from './Items/Help/Help';
import About from './Items/About/About';

const Nav = (props) => {
    return (
        <div className='nav'>
            <Projects />
            <Edit />
            <Settings />
            <Help />
            <About />
        </div>
    );
}

export default Nav;