import React from 'react';

const SavePage = (props) => {

    let deleteSave = () => {
        props.onDeleteItem(props.id)
    }

    let openProject = () => {
        props.onOpenCreaturePage(props.numberPattern, props.pattern, props.name)
    }

    return (
        <div className='save-pages' onClick={openProject}>
            <h2>Название страницы: {props.name}</h2>
            <p>Название шаблона: {props.pattern}</p>
            <button onClick={deleteSave}>
                <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="trash-alt" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path fill="#08FDD8" d="M32 464a48 48 0 0 0 48 48h288a48 48 0 0 0 48-48V128H32zm272-256a16 16 0 0 1 32 0v224a16 16 0 0 1-32 0zm-96 0a16 16 0 0 1 32 0v224a16 16 0 0 1-32 0zm-96 0a16 16 0 0 1 32 0v224a16 16 0 0 1-32 0zM432 32H312l-9.4-18.7A24 24 0 0 0 281.1 0H166.8a23.72 23.72 0 0 0-21.4 13.3L136 32H16A16 16 0 0 0 0 48v32a16 16 0 0 0 16 16h416a16 16 0 0 0 16-16V48a16 16 0 0 0-16-16z"></path></svg>
            </button>
        </div>
    );
}

export default SavePage;