import React from 'react';
import { connect } from 'react-redux';
import { onCloseModelSelect, onAddValueNextAndPrev, checkedBtn} from '../../../redux/reducers/mainReducer';
import Pattern from './Pattern';

let mapStateToProps = (state) => {
    
    return {
        name: state.main.patterns[state.main.model.item].name,
        pattern: state.main.patterns[state.main.model.item].pattern,
        selectPattern: state.main.model.item,
        patterns: state.main.patterns
    }
}


const PatternContainer = connect(mapStateToProps, {
        checkedBtn,
        onAddValueNextAndPrev,
        onCloseModelSelect
})(Pattern)

export default PatternContainer;