import React from 'react';
import './alertMessage.scss';
import smailSad from '../../../assets/images/smail-sad.svg';
import smail from '../../../assets/images/smail.svg';

const AlertMessage = (props) => {
    
    return (
        <div className={props.alertMessage === 0 ? 'alert-message' + ' null' : 'alert-message',
            props.alertMessage > 0 ? 'alert-message error' : 'alert-message' + ' null'}>


           { props.alertMessage === 1 ? <div><img src={smailSad}/><p>Не заполено название страницы!</p></div> :
             props.alertMessage === 2 ? <div><img src={smailSad}/><p>Не выбран шаблон страницы!</p></div> : <div><img src={smail}/></div>}
        </div>
    );
}

export default AlertMessage;