import React from 'react';
import './creator.scss';


const Creator = (props) => {

    const openCreaturePage = () => {
        props.onOpenCreaturePage()
    }
    
    return (
        <div className='create'>

            <div className='create_info'>
                <h1>Create page</h1>
                <p>click to create a page</p>
            </div>

            <button onClick={openCreaturePage} className='create-btn'></button>
        </div>
    );
}

export default Creator;