import React from 'react';
import AlertMessage from '../Header/AlertMessage/AlertMessage';
import ModelSelect from './ModelSelect';
import './settingsPage.scss';


const SettingsPage = (props) => {

    let newNamePage = React.createRef();

    let saveSetting = () => {
        props.onSaveSetting()
    }

    let onNameChange = () => {
        let text = newNamePage.current.value;
        props.nameChange(text)
    }

    let openModelSelect = () => {
        props.onOpenModelSelect()
    }
    
    return (

        <div className='settings-page'>
            <AlertMessage alertMessage={props.alertMessage}/>
            <div className='page-info'>

                <div className='info-item'>
                    <div>
                        <h1>Название страницы:</h1>
                        <input 
                            className={props.alertMessage === 0 ? 'name-page' : 
                                       props.alertMessage === 1 ?'name-page' + ' error' : 'name-page'} onChange={onNameChange} ref={newNamePage} value={props.newName || ''}/>
                    </div>
                    
                    <div className='patterns'>
                        <span>Шаблон страницы: </span><span 
                            className={props.windowEditPattern === 0 ? 'pattern-name' : 'pattern-name' + ' true'}>{props.namePattern}</span>
                        <button onClick={openModelSelect} 
                            className={props.alertMessage === 0 ? 'btn-select-pattern' : 
                                       props.alertMessage === 2 ?'btn-select-pattern' + ' error' : 'btn-select-pattern'}>Выбрать шаблон</button>
                    </div>
                </div>

                <button className='btn-save' onClick={ saveSetting }>сохранить</button>

            </div>

            {props.openModel ? 
                <ModelSelect  patterns={props.patterns} dispatch={props.dispatch} selectPattern={props.selectPattern}/> : null}

        </div>
    );
}

export default SettingsPage;