import React from 'react';
import SavePage from './Saves/SavePage';
import './sidebar.scss';


const Sidebar = (props) => {

    let sidebarSave, empty;
    props.sidebar.pages === null ? empty = null: sidebarSave = props.sidebar.pages.map( (el, i) => {
        return (
            <SavePage 
                name={el.name} 
                pattern={el.pattern} 
                key={el.id} 
                id={i} 
                onDeleteItem={props.onDeleteItem} 
                onOpenCreaturePage={props.onOpenCreaturePage}
                numberPattern={+el.numberPattern}/>
        )
    })

    let showSidebar = () => {
        props.onShowSidebar();
    }
    
    return (
        <>
            <div className={props.showSidebar ? 'sidebar' : 'sidebar' + ' close'}>

                <h1>Saved pages:</h1>

                {sidebarSave === null ? empty: sidebarSave}

            </div>

            <div className={props.showSidebar ? 'btn-page-block' : 'btn-page-block' + ' active'}>
                <button className={props.showSidebar ? 'show-sidebar' : 'show-sidebar' + ' active'} onClick={showSidebar}></button>
                <p>{props.showSidebar ? 'скрыть': 'показать'}</p>
            </div>
        </>
        
    );
}

export default Sidebar;